import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import FormHelperText from '@material-ui/core/FormHelperText';
import * as emailjs from 'emailjs-com';

const styles = theme => ({
  textField: {
    marginBottom: '0px',
    color: '#d3d3d3'
  },
  underline: {
    color: 'white',
    '&:after': {
      borderBottom: `2px solid gray`
    },
    '&$focused:after': {
      borderBottomColor: `white`
    },
    '&:before': {
      borderBottom: `1px solid gray`
    },
    '&:hover:not($focused):before': {
      borderBottom: `2px solid white`
    }
  },
  label: {
    color: '#aaaaaa',
    '&$focused': {
      color: 'white'
    }
  },
  focused: {},
  buttonRoot: {
    width: '90%',
    // backgroundColor: '#454545',
    color: 'white',
    borderColor: 'gray',
    '&:hover': {
      backgroundColor: '#454545',
      borderColor: 'gray'
    }
  }
});

function ContactMe(props) {
  const { classes } = props;

  const [values, setValues] = useState({
    name: '',
    email: '',
    phone: '',
    details: '',
    isLoading: false,
    sendMessage: ''
  });

  const [errors, seterrors] = useState({
    name: '',
    email: '',
    phone: '',
    details: ''
  });

  const handleInputChange = e => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const validateForm = () => {
    let errorExists = false;
    let errors = { name: '', email: '', phone: '', details: '' };

    // Name
    if (values.name.length === 0) {
      errors.name = 'Please enter your name';
      errorExists = true;
    }

    if (values.name.length < 3 && values.name.length > 0) {
      errors.name = 'Please a valid name (Longer than 3 characters)';
      errorExists = true;
    }

    // Email
    if (values.email.length === 0) {
      errors.email = 'Please enter your email';
      errorExists = true;
    }
    if (!/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(values.email)) {
      errors.email = 'Please enter a valid email';
      errorExists = true;
    }

    // Phone
    if (!/^0\d([\d]{0,1})([-]{0,1})\d{7}$/.test(values.phone)) {
      errors.phone = 'Please enter a valid phone number';
      errorExists = true;
    }

    seterrors(errors);

    return errorExists;
  };

  const sendForm = e => {
    e.preventDefault();

    setValues({ ...values, isLoading: true });

    // validate values
    const errorExists = validateForm();

    if (!errorExists) {
      let templateParams = {
        from_name: values.email,
        to_name: 'lironp90@gmail.com',
        subject: values.name + ' wants to contact you | Liron Perel Portfolio',
        message_html:
          'Message: ' +
          values.details +
          '<br /> Name: ' +
          values.name +
          '<br /> Phone: ' +
          values.phone
      };
      console.log('Sending email...');
      emailjs
        .send(
          'gmail',
          'template_JZWjnDPC',
          templateParams,
          process.env.REACT_APP_EMAILJS_ID
        )
        .then(
          function(response) {
            console.log('SUCCESS!', response.status, response.text);
            setValues({
              name: '',
              email: '',
              phone: '',
              details: '',
              sendMessage: `Thank you ${values.name}, Your message was sent :)`
            });
          },
          function(error) {
            console.log('FAILED...', error);
            setValues({
              ...values,
              sendMessage:
                'There was a problem sending your message, please try again later'
            });
          }
        );
    } else {
      setValues({ ...values, isLoading: false });
    }
  };

  return (
    <div data-aos='fade-in' data-aos-offset='150'>
      <p id='contact-me-title'>
        Contact Me
        <br />
      </p>
      <div style={{ display: 'flex', margin: '5%' }}>
        <form
          noValidate
          autoComplete='off'
          style={{
            display: 'flex',
            flex: '1',
            flexDirection: 'column',
            marginLeft: '20%',
            marginRight: '5%'
          }}
          onSubmit={sendForm}
        >
          <TextField
            onChange={handleInputChange}
            error={errors.name.length > 0}
            value={values.name}
            label='Name'
            className={classes.textField}
            name='name'
            InputLabelProps={{
              classes: {
                root: classes.label,
                focused: classes.focused
              }
            }}
            InputProps={{
              classes: {
                root: classes.textField,
                focused: classes.focused,
                underline: classes.underline
              }
            }}
            margin='dense'
          />
          <FormHelperText style={{ color: '#ff726f' }}>
            {errors.name}
          </FormHelperText>

          <TextField
            onChange={handleInputChange}
            error={errors.email.length > 0}
            value={values.email}
            label='Email'
            className={classes.textField}
            type='email'
            name='email'
            InputLabelProps={{
              classes: {
                root: classes.label,
                focused: classes.focused
              }
            }}
            InputProps={{
              classes: {
                root: classes.textField,
                focused: classes.focused,
                underline: classes.underline
              }
            }}
            margin='dense'
          />

          <FormHelperText style={{ color: '#ff726f' }}>
            {errors.email}
          </FormHelperText>

          <TextField
            onChange={handleInputChange}
            error={errors.phone.length > 0}
            value={values.phone}
            label='Phone'
            className={classes.textField}
            name='phone'
            InputLabelProps={{
              classes: {
                root: classes.label,
                focused: classes.focused
              }
            }}
            InputProps={{
              classes: {
                root: classes.textField,
                focused: classes.focused,
                underline: classes.underline
              }
            }}
            margin='dense'
          />
          <FormHelperText style={{ color: '#ff726f' }}>
            {errors.phone}
          </FormHelperText>

          <TextField
            onChange={handleInputChange}
            error={errors.details.length > 0}
            value={values.details}
            label='Details'
            className={classes.textField}
            name='details'
            InputLabelProps={{
              classes: {
                root: classes.label,
                focused: classes.focused
              }
            }}
            InputProps={{
              classes: {
                root: classes.textField,
                focused: classes.focused,
                underline: classes.underline
              }
            }}
            margin='dense'
            multiline={true}
            rows='7'
          />
          <FormHelperText style={{ color: '#ff726f' }}>
            {errors.details}
          </FormHelperText>
        </form>
        <div
          style={{
            display: 'flex',
            flex: '1',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column'
          }}
        >
          <Button
            disabled={values.isLoading}
            variant='outlined'
            color='primary'
            type='submit'
            endIcon={<Icon>send</Icon>}
            classes={{
              root: classes.buttonRoot
            }}
            onClick={sendForm}
          >
            Send
          </Button>
          {/* {values.sendMessage && ( */}
          <FormHelperText style={{ color: 'lightgreen' }}>
            {values.sendMessage}
          </FormHelperText>
          {/* )} */}
        </div>
      </div>
    </div>
  );
}

ContactMe.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ContactMe);
